'use client';

import React, { useEffect, useState } from 'react';
import { supabase } from '@/lib/supabase';
import { useRouter } from 'next/navigation';
import { 
  ShieldCheck, Key, History, Download, Zap, User, 
  Loader2, LogOut, CheckCircle2 
} from 'lucide-react';

export default function DashboardPage() {
  const router = useRouter();
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [message, setMessage] = useState<{ type: 'success' | 'error', text: string } | null>(null);
  
  // State Profile lengkap sesuai kolom di tabel profiles
  const [profile, setProfile] = useState({
    id: '',
    full_name: '',
    is_pro: false,
    openai_key: '',
    anthropic_key: '',
    gemini_key: '',
    mistral_key: '',
    llama_key: ''
  });

  // 1. Ambil data profil saat halaman dibuka
  useEffect(() => {
    const fetchProfile = async () => {
      const { data: { user } } = await supabase.auth.getUser();
      
      if (!user) {
        router.push('/auth/login');
        return;
      }

      const { data } = await supabase
        .from('profiles')
        .select('*')
        .eq('id', user.id)
        .single();

      if (data) {
        setProfile({
          id: data.id,
          full_name: data.full_name || '',
          is_pro: data.is_pro || false,
          openai_key: data.openai_key || '',
          anthropic_key: data.anthropic_key || '',
          gemini_key: data.gemini_key || '',
          mistral_key: data.mistral_key || '',
          llama_key: data.llama_key || ''
        });
      }
      setLoading(false);
    };

    fetchProfile();
  }, [router]);

  // 2. Fungsi simpan data ke Vault
  const handleUpdateKeys = async () => {
    setSaving(true);
    setMessage(null);
    
    const { error } = await supabase
      .from('profiles')
      .update({
        openai_key: profile.openai_key,
        anthropic_key: profile.anthropic_key,
        gemini_key: profile.gemini_key,
        mistral_key: profile.mistral_key,
        llama_key: profile.llama_key
      })
      .eq('id', profile.id);

    if (error) {
      setMessage({ type: 'error', text: 'Failed to update vault.' });
    } else {
      setMessage({ type: 'success', text: 'Intelligence Vault updated successfully.' });
      setTimeout(() => setMessage(null), 3000);
    }
    setSaving(false);
  };

  const handleSignOut = async () => {
    await supabase.auth.signOut();
    router.push('/');
  };

  // URL Checkout dengan ID user untuk tracking otomatis
  const checkoutUrl = `${process.env.NEXT_PUBLIC_LEMON_SQUEEZY_CHECKOUT_URL}?checkout[custom][user_id]=${profile.id}`;

  if (loading) return (
    <div className="min-h-screen bg-[#FDFCFB] flex items-center justify-center">
      <Loader2 className="animate-spin text-[#D4AF37] w-12 h-12" />
    </div>
  );

  return (
    <div className="min-h-screen bg-[#FDFCFB] text-black pb-32">
      {/* 1. DASHBOARD HEADER */}
      <section className="max-w-6xl mx-auto pt-24 pb-12 px-8">
        <div className="flex flex-col md:flex-row justify-between items-end gap-8">
          <div className="space-y-4">
            <div className={`inline-flex items-center gap-2 px-4 py-1.5 rounded-full text-[12px] font-black uppercase tracking-[0.3em] ${profile.is_pro ? 'bg-black text-[#D4AF37]' : 'bg-gray-100 text-gray-400'}`}>
              <ShieldCheck className="w-4 h-4" /> {profile.is_pro ? 'Verified Pro Account' : 'Standard Account'}
            </div>
            <h1 className="text-5xl font-serif italic text-black leading-tight">Command <span className="text-[#D4AF37]">Center.</span></h1>
            <p className="text-[16px] text-gray-400 font-light italic leading-relaxed">Manage your elite fiscal intelligence assets and API integration.</p>
          </div>
          <div className="flex gap-4 no-print items-center">
            {!profile.is_pro && (
              <a href={checkoutUrl} className="px-8 py-3 bg-[#D4AF37] text-black rounded-full text-[14px] font-black uppercase tracking-widest hover:bg-black hover:text-[#D4AF37] transition-all flex items-center gap-3 shadow-lg shadow-gold/20">
                <Zap size={16} /> Upgrade Pro
              </a>
            )}
            <button onClick={handleSignOut} className="px-8 py-3 bg-white border border-gray-100 rounded-full text-[14px] font-black uppercase tracking-widest hover:bg-gray-50 transition-all flex items-center gap-3">
              <LogOut size={16} /> Sign Out
            </button>
          </div>
        </div>
      </section>

      {/* 2. THE VAULT: API KEY MANAGEMENT */}
      <section className="max-w-6xl mx-auto px-8 mb-20">
        <div className="bg-white border border-gray-100 rounded-[3rem] p-12 shadow-2xl shadow-gray-100/50">
          <div className="flex items-center justify-between mb-12 border-b border-gray-50 pb-6">
            <div className="flex items-center gap-4">
              <Key className="text-[#D4AF37]" />
              <h2 className="text-[14px] font-black uppercase tracking-[0.4em]">Intelligence API Vault</h2>
            </div>
            {message && (
              <div className={`text-[13px] font-bold flex items-center gap-2 ${message.type === 'success' ? 'text-green-600' : 'text-red-500'}`}>
                {message.type === 'success' && <CheckCircle2 size={16} />} {message.text}
              </div>
            )}
          </div>
          
          <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
            {[
              { id: 'openai_key', provider: "OpenAI", model: "GPT-5.2 / o3" },
              { id: 'anthropic_key', provider: "Anthropic", model: "Claude 4.5 Opus" },
              { id: 'gemini_key', provider: "Google Gemini", model: "Gemini 3 Ultra" },
              { id: 'mistral_key', provider: "Mistral AI", model: "Mistral Large 3" },
              { id: 'llama_key', provider: "Meta Llama", model: "Llama 4 (70B)" }
            ].map((api, i) => (
              <div key={i} className="p-8 bg-[#FDFCFB] rounded-3xl border border-gray-50 space-y-6 group hover:border-[#D4AF37]/30 transition-all">
                <div className="flex justify-between items-start">
                  <span className="text-[14px] font-bold text-black">{api.provider}</span>
                  <div className={`px-3 py-1 rounded-full text-[9px] font-black uppercase tracking-widest ${(profile as any)[api.id] ? 'bg-green-50 text-green-600' : 'bg-gray-100 text-gray-400'}`}>
                    {(profile as any)[api.id] ? 'Active' : 'Inactive'}
                  </div>
                </div>
                <div className="space-y-4">
                  <p className="text-[12px] text-gray-400 uppercase tracking-widest leading-relaxed">Target Model: <span className="text-black font-bold">{api.model}</span></p>
                  <input 
                    type="password" 
                    value={(profile as any)[api.id]}
                    onChange={(e) => setProfile({ ...profile, [api.id]: e.target.value })}
                    placeholder="Enter API Key"
                    className="w-full bg-white border border-gray-100 rounded-xl py-3 px-4 text-[14px] focus:outline-none focus:border-[#D4AF37] transition-all"
                  />
                </div>
              </div>
            ))}
          </div>
          <div className="mt-12 flex justify-end">
             <button 
               onClick={handleUpdateKeys}
               disabled={saving}
               className="px-12 py-5 bg-black text-[#D4AF37] rounded-2xl text-[14px] font-black uppercase tracking-[0.2em] hover:bg-[#D4AF37] hover:text-black transition-all shadow-xl shadow-gold/10 flex items-center gap-3 disabled:opacity-50"
             >
               {saving ? <Loader2 className="animate-spin" size={18} /> : 'Update All Vault Keys'}
             </button>
          </div>
        </div>
      </section>

      {/* 3. REPORT ARCHIVE */}
      <section className="max-w-6xl mx-auto px-8">
        <div className="bg-white border border-gray-100 rounded-[3rem] p-12 shadow-sm">
          <div className="flex items-center gap-4 mb-12">
            <History className="text-[#D4AF37]" />
            <h2 className="text-[14px] font-black uppercase tracking-[0.4em]">Recent Analysis Archive</h2>
          </div>
          
          <div className="overflow-x-auto">
            <table className="w-full text-left">
              <thead>
                <tr className="border-b border-gray-50">
                  <th className="pb-6 text-[12px] font-black uppercase tracking-widest text-gray-400">Jurisdiction</th>
                  <th className="pb-6 text-[12px] font-black uppercase tracking-widest text-gray-400">Date Generated</th>
                  <th className="pb-6 text-[12px] font-black uppercase tracking-widest text-gray-400">AI Model Used</th>
                  <th className="pb-6 text-right text-[12px] font-black uppercase tracking-widest text-gray-400">Action</th>
                </tr>
              </thead>
              <tbody className="divide-y divide-gray-50">
                {[
                  { country: "USA to Indonesia", date: "Jan 03, 2026", model: "GPT-5.2" },
                  { country: "UK to Singapore", date: "Jan 01, 2026", model: "Claude 4.5" },
                  { country: "Germany to USA", date: "Dec 28, 2025", model: "Gemini 3" }
                ].map((row, i) => (
                  <tr key={i} className="group hover:bg-[#FDFCFB]">
                    <td className="py-6 text-[14px] font-bold">{row.country}</td>
                    <td className="py-6 text-[14px] text-gray-500 font-light">{row.date}</td>
                    <td className="py-6 text-[14px] font-serif italic text-[#D4AF37]">{row.model}</td>
                    <td className="py-6 text-right">
                      <button className="inline-flex items-center gap-2 text-[12px] font-black uppercase tracking-widest text-gray-400 hover:text-black transition-colors">
                        <Download size={14} /> Re-Export PDF
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      </section>
    </div>
  );
}